body{
    color: rgb(47, 47, 48);
    background-color: rgb(240, 238, 235);
    /* background-color: rgb(65, 63, 63); */
}
.containerww{
    display: flex;
    justify-content: center;
    margin: 20px;

}
.cart_item{
       width: 550px;
    height: 275px;
    background-color: rgba(171, 172, 173, 0.474);
    border: 1px solid rgb(47, 47,47);
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 6px rgb(89, 89, 89);
}
img{
    width: 44%;
    object-fit: contain;
}
.countHandler{
    width:  217px;
}
.countHandler button{
    background-color:rgb(19, 18, 18) ;
    color:rgb(225, 217, 217);
}
.countHandler input{
    width: 28px;
    text-align: center;
    font-size: larger;
    border: none;
    background-color: transparent;
}
.checkout button{

    color: darkblue;
    border: 1px solid darkblue;
    background-color: white;
    transition: all ease-in-out .2s;
    border-radius: 12px;
    padding: 8px 10px;
    margin:10px
}
.checkout button:hover{
    background-color: darkblue;
    border: 1px solid black;
    color: white;

}