.products{
display:grid;
grid-template-columns:repeat(4,310px);
grid-gap:2px;
margin:30px;

}
.product-image{
width:350px;
height:200px;
width:100%;
object-fit:contain;
}
.product{
    margin: 10px;
    background-color:#f6f6f6;
    border-radius: 5px;
    transition: all ease-in-out .2s;
}
.product:hover{
    cursor: pointer;
    transform: scale(1.01);
    box-shadow: 0px 0px 16px rgb(26, 26, 26);
}
.addtocartbtn{
    border: 1px solid rgb(33, 33, 33);
    border-radius: 10px;
    padding: 5px 10px;
    margin-bottom: 10px;
    background-color: white;
    color: rgb(33, 33, 33);
    transition: all ease-in-out .2s;
    cursor: pointer;
}
.addtocartbtn:hover{
    background-color:rgb(33, 33, 33) ;
    color:white
}
.visible {
    visibility: visible;
    transition: all ease-in-out .2s;
  }
  
  .hidden {
    visibility: hidden;
    transition: all ease-in-out .2s;
  }