.navbar{
    width: 100%;height: 80px;
    background-color: rgb(30, 30, 32);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    box-shadow: 0px 4px 20px rgb(88, 85, 85);
}

.links{
    margin-right: 50px;
    margin-right: 20px;
    display: flex;
    align-items: center;
}
.links a{
    text-decoration: none;
    margin-left: 20px;
    font-size:  25px;
    color: white;
}
.links a:hover{
   
    color: #53b3df;
}